import defaultConfig from '../appConfig';
import { sendAliLog, aliLog} from '../common/ali_log';
import C, {LR_DRAINAGE_CONFIG_KEY, CHATGPT_AUTO_DOM_CONFIG_KEY} from '../common/constant'

const reqDomain = process.env.VUE_APP_LINKEDRADAR_HOST;

// Key
const configKey = [LR_DRAINAGE_CONFIG_KEY, CHATGPT_AUTO_DOM_CONFIG_KEY];
// ID
const zBaseExtensionId = 46;

const timestampStoreKey = 'gpt_common_remote_config_req_record_timestamp';

const remoteConfigHandler = (configList) => {
  configKey.forEach((k) => {
    let configValue = configList.find((item) => item.name === k) || {};
    console.log(k, '', configValue.params);
    chrome.storage.local.set({ [k]: configValue.params || {} });
  });
};

const getRequestTimestampFromStorage = async () => {
  let data = await browser.storage.local.get([timestampStoreKey]);
  return data ? data[timestampStoreKey] || 0 : 0;
};

const setRequestTimestampToStorage = async () => {
  let currentTimeStamp = Math.floor(Date.now() / 1000);
  await browser.storage.local.set({ [timestampStoreKey]: currentTimeStamp });
};

/**
 * 
 */
async function getRemoteConfig(successCallback) {
  let resp = {};
  try {
    let response = await fetch(
      `${reqDomain}/user/api-chrome-extension/get-remote-config?source=gpt-common`,
      {
        method: 'POST',
        headers: { 'Content-type': 'application/json', "Host": "linkedradar.com" },
        body: JSON.stringify({ extension_id: zBaseExtensionId }),
      }
    );
    resp = await response.json();
  } catch (error) {
    console.log(error.message, '...');
  }
  // TODO
  // resp = {
  //   code: 100000,
  //   data: {
  //     zBaseExtensionId,
  //     config: [
  //       {
  //         name: 'paid_functional_verification',
  //         params: { viral_post: true },
  //       },
  //       {
  //         name: 'drainage',
  //         params: { automation_tool: true, header: false, profile: false},
  //       },
  //     ],
  //   },
  // };
  if (resp.code === 100000 && resp.data) {
    console.log(' -> ', resp.data);
    remoteConfigHandler(resp.data.config || []);
    aliLog(`${C.LOG_TYPE}0113`)
    if (successCallback) successCallback();
  } else {
    console.log('...');
    aliLog(`${C.LOG_TYPE}0114`, '', {req_result: resp})
    await setRequestTimestampToStorage();
  }
}

/**
 * storage
 * @param configKey key
 */
export async function loadAppConfig(configKey) {
  let data = await chrome.storage.local.get([configKey]);
  // console.log(': ', data);
  return data ? (data[configKey] || {}) : {}
}

/**
 * 
 */
export function getConfigByStepKey(config, configKey, stepKey) {
  let stepValue = config[stepKey]
  if (stepValue) return stepValue;

  // , 
  let defaultConfigValue = defaultConfig[configKey];
  if (defaultConfigValue) {
    return defaultConfigValue[stepKey];
  } else {
    sendAliLog(`${C.LOG_TYPE}0900`, '', {
      config_key: configKey,
      step_key: stepKey
    });
  }
}

export async function asyncGetConfigByStepKey(configKey, stepKey) {
  let config = await loadAppConfig(configKey)
  return getConfigByStepKey(config, configKey, stepKey)
}

/**
 * 
 */
export async function updateRemoteConfigEventHandler() {
  const reqInterval = 60 * 60 * 6; // 6

  let preRequestTimestamp = await getRequestTimestampFromStorage();
  let nowTimestamp = Math.floor(Date.now() / 1000);

  if (nowTimestamp - Number(preRequestTimestamp) < reqInterval) {
    console.log(
      `${reqInterval}${preRequestTimestamp}${nowTimestamp}`
    );
    return;
  }

  await getRemoteConfig(setRequestTimestampToStorage);
}
