/**  **/

import SlsWebLogger from 'js-sls-logger-v3';

//
const ali_log_opts = {
  host: process.env.VUE_APP_ALILOG_HOST,
  project: process.env.VUE_APP_ALILOG_PROJECT,
  logstore: process.env.VUE_APP_ALILOG_LOGSTORE,
  time: 1,
  count: 1,
};

const logger = new SlsWebLogger(ali_log_opts);

const EDGE_PATTERN = /edge\/([\d.]+)|edg\/([\d.]+)/;
const IE_PATTERN1 = /rv:([\d.]+)\) like gecko/;
const IE_PATTERN2 = /msie ([\d.]+)/;
const FIREFOX_PATTERN = /firefox\/([\d.]+)/;
const CHROME_PATTERN = /chrome\/([\d.]+)/;
const OPERA_PATTERN = /opera.([\d.]+)/;
const SAFARI_PATTERN = /version\/([\d.]+).*safari/;

export function getBrowserType() {
  let type = 'other';
  let ua = navigator.userAgent.toLowerCase();
  if (EDGE_PATTERN.test(ua)) {
    type = 'Edge';
  } else if (IE_PATTERN1.test(ua)) {
    type = 'IE';
  } else if (IE_PATTERN2.test(ua)) {
    type = 'IE';
  } else if (FIREFOX_PATTERN.test(ua)) {
    type = 'Firefox';
  } else if (CHROME_PATTERN.test(ua)) {
    type = 'Chrome';
  } else if (OPERA_PATTERN.test(ua)) {
    type = 'Opera';
  } else if (SAFARI_PATTERN.test(ua)) {
    type = 'Safari';
  }
  return type;
}

export function guid() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    let r = (Math.random() * 16) | 0,
      v = c == 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

export async function getUUID() {
  let data = await chrome.storage.local.get(['uuid']);
  if (data.uuid == undefined) {
    let uuid = guid();
    await chrome.storage.local.set({ uuid: uuid });
    return uuid;
  } else {
    return data.uuid;
  }
}

export async function getClientVersion() {
  let options = chrome.runtime.getManifest();
  return options.version;
}

export async function setInstallTime() {
  let data = await chrome.storage.local.get(['install_time']);
  if (data.install_time == undefined) {
    let install_time = Math.floor(Date.now() / 1000);
    await browser.storage.local.set({ install_time: install_time });
  }
}

export async function getInstallTime() {
  let data = await chrome.storage.local.get(['install_time']);
  if (data.install_time == undefined) {
    return 0;
  } else {
    return data.install_time;
  }
}

export async function sendAliLog(...argv) {
  await chrome.runtime.sendMessage({
    code: 'alilog_send',
    event_type: argv[0],
    zhihu_profile: argv[1],
    event_data: argv[2],
  });
}

/*
 * event_type, param_data
 */
export async function aliLog(...argv) {
  let uuid = await getUUID();
  let install_time = await getInstallTime();
  let client_version = await getClientVersion();
  let user_email = '';
  let userUrl = '';

  let storageData = await chrome.storage.local.get([
    'platform',
    'browserType',
    'language',
    'userAgent',
  ]);

  let log_data = {
    event_source: '2c',
    event_type: argv[0],
    install_time: install_time, //
    event_time: Math.floor(Date.now() / 1000), //
    log_version: 'v1', //
    uuid: uuid,
    zhihu_profile: '',
    zhihu_url: userUrl,
    user_email: user_email,
    version: client_version,
    platform: storageData.platform ? storageData.platform : '',
    browser_type: storageData.browserType ? storageData.browserType : '',
    language: storageData.language ? storageData.language : '',
    user_agent: storageData.userAgent ? storageData.userAgent : '',
  };

  if (argv[2]) {
    for (let key in argv[2]) {
      log_data[key] = argv[2][key];
    }
  }

  logger.send(log_data);
}
