/**  **/
export function setStgGPTApiKey(api_ley) {
  return new Promise((resolve) => {
    chrome.storage.local.set({ gpt_api_key: api_ley }, function () {
      resolve(true);
    });
  });
}

/**  **/
export function getStgGPTApiKey() {
  return new Promise((resolve) => {
    chrome.storage.local.get('gpt_api_key', function (result) {
      let data = result.gpt_api_key ? result.gpt_api_key : '';
      resolve(data);
    });
  });
}

/**  **/
export function setStgAskForGPTText(text) {
  return new Promise((resolve) => {
    chrome.storage.local.set({ ask_for_chatgpt_text: text }, function () {
      resolve(true);
    });
  });
}

/**  **/
export function getStgAskForGPTText() {
  return new Promise((resolve) => {
    chrome.storage.local.get('ask_for_chatgpt_text', function (result) {
      let data = result.ask_for_chatgpt_text ? result.ask_for_chatgpt_text : '';
      resolve(data);
    });
  });
}
