import { getStgAskForGPTText, setStgAskForGPTText } from '../common/storage';
import C, {
  LR_DRAINAGE_CONFIG_KEY,
  CHATGPT_AUTO_DOM_CONFIG_KEY,
  SYSTEM_THEME_DARK_MODE,
  CHATGPT_THEME_LOCALSTORAGE_KEY,
} from '../common/constant';
import {
  elementInputTextSimulation,
  inputSubmitSimulation,
  sleepAwaitMS,
} from '../utils/simulation';
import { sendAliLog } from '../common/ali_log';
import { getConfigByStepKey, loadAppConfig } from '../utils/remoteConfig';
import { getSystemThemeMode } from '../utils/utils';

sendAliLog(`${C.LOG_TYPE}0242`);

let question; //
let getDomSelectorConfig = undefined; // DOM
let getDrainageConfig = undefined; // 

async function mockInputAndSend(text) {
  let textarea = document.querySelector(
    getDomSelectorConfig('chatgpt_question_textarea_dom')
  );
  let button = document.querySelector(
    getDomSelectorConfig('chatgpt_question_submit_dom')
  );

  if (textarea) {
    await elementInputTextSimulation(textarea, text);

    if (button) {
      await inputSubmitSimulation(button);
      return true;
    }
  }
  return false;
}

// 
function createCopyButton() {
  let buttonParent = document.querySelector(
    getDomSelectorConfig('chatgpt_answer_copy_button_achor_dom')
  );

  let button = document.createElement('div');
  button.style.cssText = `display: flex;background-color: #10182b;justify-content: center;align-items: center;width: 140px;height: 40px;
  border-radius: 8px;cursor: pointer;${getDomSelectorConfig(
    'chatgpt_answer_copy_button_style'
  )}`;
  button.innerHTML = `
    <p style="color: white; font-size: 18px; font-weight: 500">Copy To</p>
    <div style="width: 30px; height: 30px; margin-left: 5px;">
      <img src="" />
    </div>
  `;

  const logoImg = chrome.runtime.getURL('icons/logo.png');
  const img = button.getElementsByTagName('img')[0];
  img.src = logoImg;

  button.onclick = copyClick;

  buttonParent?.appendChild(button);
}

function copyClick() {
  const responseElement = document.querySelector(
    getDomSelectorConfig('chatgpt_answer_content_dom')
  );
  if (responseElement) {
    const textContent = responseElement.innerHTML || '';
    chrome.runtime.sendMessage({
      type: C.MESSAGE_FROM_CHATGPT_COPY,
      text: textContent,
    });
    sendAliLog(`${C.LOG_TYPE}0245`, '', { question, response: textContent });
  }
}

// 
async function injectDrainageSection() {
  const LIGHT_THEME_CLASS_NAME = 'light-zing-gpt';
  const DARK_THEME_CLASS_NAME = 'dark-zing-gpt';
  const CHATGPT_LIGHT_THEME = 'light';
  const CHATGPT_DARK_THEME = 'dark';
  const CHATGPT_SYSTEM_THEME = 'system';

  // chatgpt  dark/light/system
  const theme = localStorage.getItem(CHATGPT_THEME_LOCALSTORAGE_KEY);

  function createStyleSheet() {
    const styleEle = document.createElement('style');

    const css = `
    .zing-gpt-drainage-button-text {text-overflow: ellipsis;overflow: hidden;white-space: nowrap;}
    .zing-gpt-drainage-button {display:flex;justify-content: space-between;  border: 1px solid rgba(0,0,0,.1);  font-size: .875rem; line-height: 1.25rem;padding: 0.5rem 0.75rem;border-radius: 0.75rem;color: #40414f;cursor: pointer;}
    .zing-gpt-drainage-button:hover {background-color: #ececf1; }
    .zing-gpt-drainage-button:hover .zing-gpt-drainage-button-right {display: flex }
    .zing-gpt-drainage-button-left {display: flex; flex-direction: column; overflow: hidden}
    .zing-gpt-drainage-button-right {display: none; justify-content: center; align-items: center}
    .light-zing-gpt .zing-gpt-drainage-button {border-color: rgba(0,0,0,.1); color: #40414f}
    .light-zing-gpt .zing-gpt-drainage-button:hover {background-color: #ececf1;}
    .dark-zing-gpt .zing-gpt-drainage-button {border-color: rgba(86,88,105,1); color: rgba(197,197,210,1)}
    .dark-zing-gpt .zing-gpt-drainage-button:hover {background-color: rgba(64,65,79,1)}
    `;

    if (styleEle.styleSheet) {
      // IE
      styleEle.styleSheet.cssText = css;
    } else {
      // 
      styleEle.appendChild(document.createTextNode(css));
    }

    document.head.appendChild(styleEle);
  }

  function toggleElementTheme(element, oldTheme, newTheme) {
    element.className.includes(oldTheme) && element.classList.remove(oldTheme);

    element.classList.add(newTheme);
  }

  function setDrainageButtonTheme(element) {
    console.log(theme, '================');
    if (theme === CHATGPT_LIGHT_THEME) {
      toggleElementTheme(
        element,
        DARK_THEME_CLASS_NAME,
        LIGHT_THEME_CLASS_NAME
      );
    } else if (theme === CHATGPT_DARK_THEME) {
      toggleElementTheme(
        element,
        LIGHT_THEME_CLASS_NAME,
        DARK_THEME_CLASS_NAME
      );
    } else if (theme === CHATGPT_SYSTEM_THEME) {
      const systemThemeMode = getSystemThemeMode();
      systemThemeMode === SYSTEM_THEME_DARK_MODE
        ? toggleElementTheme(
            element,
            LIGHT_THEME_CLASS_NAME,
            DARK_THEME_CLASS_NAME
          )
        : toggleElementTheme(
            element,
            DARK_THEME_CLASS_NAME,
            LIGHT_THEME_CLASS_NAME
          );
    }
  }

  function createDrainageButton(button, index) {
    const achorEle = document.querySelectorAll(
      getDrainageConfig('chatgpt_page_button_dom_achor')
    )[index];

    if (achorEle) {
      const drainageButtonId = `zing-gpt-drainage-button-${index}`;

      achorEle.insertAdjacentHTML(
        'beforebegin',
        `
        <div class="zing-gpt-drainage-button-container" id="${drainageButtonId}">
          <div class="zing-gpt-drainage-button">
            <div class="zing-gpt-drainage-button-left">
              <div class="zing-gpt-drainage-button-text" style="font-weight: 600; ">${button.text}</div>
              <div class="zing-gpt-drainage-button-text" style="color: #9fa0a7">${button.description}</div>
            </div>
            <div class="zing-gpt-drainage-button-right">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" fill="none" class="h-4 w-4" stroke-width="2"><path d="M.5 1.163A1 1 0 0 1 1.97.28l12.868 6.837a1 1 0 0 1 0 1.766L1.969 15.72A1 1 0 0 1 .5 14.836V10.33a1 1 0 0 1 .816-.983L8.5 8 1.316 6.653A1 1 0 0 1 .5 5.67V1.163Z" fill="currentColor"></path></svg>
            </div>
          </div>
        </div>
        `
      );

      const createdbuttonEle = document.getElementById(drainageButtonId);

      // 
      setDrainageButtonTheme(createdbuttonEle);

      // 
      createdbuttonEle?.addEventListener('click', () => {
        sendAliLog(`${C.LOG_TYPE}0282`, '', { text: button.text });
        window.open(button.link_url);
      });
    }
  }

  // 
  const showChatgptButton = getDrainageConfig('chatgpt_page_switch');
  if (!showChatgptButton) return;

  // , , 
  const drainageButtonList = document.querySelectorAll(
    '.zing-gpt-drainage-button-container'
  );
  if (drainageButtonList.length) {
    // chatgpt
    for (let i = 0; i < drainageButtonList.length; i++) {
      setDrainageButtonTheme(drainageButtonList[i]);
    }

    return;
  }

  // , 
  const isHasRegenerateButton = document.querySelectorAll(
    getDrainageConfig('chatgpt_page_button_dom_achor')
  );
  if (isHasRegenerateButton.length === 1) return;

  // 
  createStyleSheet();

  const buttonList = getDrainageConfig('chatgpt_page_button');
  buttonList.forEach((item, index) => {
    createDrainageButton(item, index * 2);
  });
}

async function mountRequestChatGPT() {
  try {
    await sleepAwaitMS(500, 1000);

    const data = await getStgAskForGPTText();
    question = data;
    sendAliLog(`${C.LOG_TYPE}0243`, '', { question: data });
    console.log('ask_for_chatgpt_text: ', data);
    if (data) {
      let inputSuccess = await mockInputAndSend(data);
      if (inputSuccess) {
        setTimeout(() => {
          createCopyButton();
          //  ask_for_chatgpt_text ,  chatGPT  storage
          setStgAskForGPTText('');
        }, 3000);
      }
    }
  } catch (error) {
    console.log(error);
  }
}

function sendMessage() {
  chrome.runtime.sendMessage({ type: C.MESSAGE_SYNC_CONFIG }); // 
}

async function injectScript() {
  getDomSelectorConfig = getConfigByStepKey.bind(
    null,
    await loadAppConfig(CHATGPT_AUTO_DOM_CONFIG_KEY),
    CHATGPT_AUTO_DOM_CONFIG_KEY
  );
  getDrainageConfig = getConfigByStepKey.bind(
    null,
    await loadAppConfig(LR_DRAINAGE_CONFIG_KEY),
    LR_DRAINAGE_CONFIG_KEY
  );
  // chatgpt
  mountRequestChatGPT();

  // 
  setInterval(() => {
    injectDrainageSection();
  }, 2000);

  sendMessage();
}

injectScript();
