import {SYSTEM_THEME_LIGHT_MODE, SYSTEM_THEME_DARK_MODE} from '../common/constant'

export function getSystemThemeMode() {
  const isDarkMode =
    window.matchMedia &&
    window.matchMedia('(prefers-color-scheme: dark)').matches;
  // const isLightMode =
  //   window.matchMedia &&
  //   window.matchMedia('(prefers-color-scheme: light)').matches;

  return isDarkMode ? SYSTEM_THEME_DARK_MODE : SYSTEM_THEME_LIGHT_MODE
}
